/* eslint-disable @typescript-eslint/no-unused-vars */
import { CommonModule } from '@angular/common';
import { ChangeDetectionStrategy, Component, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges } from '@angular/core';
import { CardModule } from 'primeng/card';
import { ImageModule } from 'primeng/image';
import { TabMenuModule } from 'primeng/tabmenu';
import { TabViewModule } from 'primeng/tabview';
import { BadgeModule } from 'primeng/badge';
import { ButtonModule } from 'primeng/button';
import { FieldsetModule } from 'primeng/fieldset';
import { AvatarModule } from 'primeng/avatar';
import { AvatarGroupModule } from 'primeng/avatargroup';
import { IPostFile, IPostFull, Post } from '@ApiClient/backend/Post/post';
import { IPostService } from '@ApiClient/services/post';
import { ToService } from '@ApiClient/helpers/observables';
import { Observable, Subscription, filter, first, isEmpty, take } from 'rxjs';
import { RatingComponent } from '@modules/rating/rating.component';
import { Store } from '@ngrx/store';
import { selectPostById, selectPostStatusById } from '@store/feed/feed.selectors';
import { SkeletonModule } from 'primeng/skeleton';
import { DeferModule } from 'primeng/defer';
import { TextMultiLangComponent } from '@ApiClient/components/text-multi-lang/text-multi-lang.component';
import * as fromPostActions from '@store/feed/feed.actions';

@Component({
  selector: 'app-post',
  standalone: true,
  imports: [
    CommonModule,
    CardModule,
    ImageModule,
    TabMenuModule,
    TabViewModule,
    BadgeModule,
    ButtonModule,
    FieldsetModule,
    AvatarModule,
    AvatarGroupModule,
    RatingComponent,
    SkeletonModule,
    DeferModule,
    TextMultiLangComponent,
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './post.component.html',
  styleUrl: './post.component.scss',
})
export class PostComponent implements OnInit, OnDestroy, OnChanges {
  @Input() postId!: string;

  postInfo$!: Observable<IPostFull>;
  postStatus$!: Observable<string>;
  urlThumbnail$!: Observable<string>;

  private subscription!: Subscription;

  postService: IPostService;
  partialItem: Partial<IPostFile> = { id: '', fileId: '', userCreatorId: '' };

  isLoading: boolean = false;
  activeTabIndex: number = 0;
  chevronUP: boolean = false;

  constructor(private store: Store) {
    this.postService = ToService<IPostService>(Post);
  }

  async ngOnInit(): Promise<void> {
    // eslint-disable-next-line @ngrx/no-store-subscription
    this.postInfo$.pipe(isEmpty(), first()).subscribe((isEmpty) => {
      if (isEmpty) {
        this.store.dispatch(fromPostActions.deleteFeedPost({ postId: this.postId }));
      }
    });
    this.postStatus$ = this.store.select(selectPostStatusById(this.postId));
    this.subscription = this.postStatus$.subscribe((status) => {
      this.isLoading = status === 'loading';
    });
    this.subscription = this.postStatus$
      .pipe(
        filter((status) => status === 'correct'),
        take(1),
      )
      .subscribe(() => {
        this.postInfo$.pipe(take(1)).subscribe((post) => {
          this.partialItem.fileId = post.postFiles[0].fileId;
          this.partialItem.userCreatorId = post.userCreatorId;
          this.partialItem.id = post.postFiles[0].id;
        });
      });
    this.setUpImage();
  }

  ngOnChanges(changes: SimpleChanges) {
    if ('postId' in changes) {
      this.postInfo$ = this.store.select(selectPostById(this.postId));
      this.subscription = this.postInfo$.subscribe((post) => {
        this.setUpImage();
      });
    }
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  setUpImage() {
    this.urlThumbnail$ = this.postService.getUrlPostFileThumbnail(this.partialItem);
  }

  onTabChange(event: { index: number }) {
    if (event.index === 0) {
      this.chevronUP = false;
    } else {
      this.chevronUP = true;
    }
  }
}
